h1 {
  font-size: 4rem;
}

form {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  gap: 20px;

  label {
    grid-column: 1/4;
    font-size: 2rem;
  }

  .error-message {
    color: var(--chakra-colors-error-base);
    font-size: 1.3rem;
  }
}

@media (max-width: 991px) {
  form {
    grid-template-columns: repeat(2, minmax(250px, 1fr));

    label {
      grid-column: 1/3;
      font-size: 1.6rem;
    }
  }
}

@media (max-width: 767px) {
  html {
    font-size: 30%;
  }

  form {
    grid-template-columns: repeat(1, minmax(150px, 1fr));

    label {
      grid-column: 1/2;
    }
  }
}
