html {
  overflow-y: scroll;
  //Faz com que 1rem = 10px
  font-size: 62.5%;
}

body {
  font-family: 'Open sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

@media (max-width: 991px) {
  body {
    padding: 20px 15px;
  }
}

#root {
  height: 100%;
}

.App {
  margin: 40px 0;
}
